@import "src/styles/placement.scss";
@import "src/styles/colors.scss";


.about{
    
    background-color: $color-primary;
    color: $color-placeholder;
    text-align: left;
    padding: 0% 10% 10%;
    overflow-x: hidden;
    

    h2{
        position: relative;
        font-size: 55px;
        margin-bottom: 80px;
        z-index: 1;
        &::after{
            position: absolute;
            content: '';
            height:20px;
            background-color: $color-tertiary;
            width: 100%;
            bottom: -5px;
            left: 30px;
            display: block;
            /* transform-origin: bottom; */
            
            z-index: -1 ;
            
        }
    }
    

    div{
        
        p{
            flex: 1;
            line-height: 40px;
            font-size: 15px;
            margin-top: 0;

            a{
                text-decoration: none;
                color: $color-tertiary;
                transition: border-bottom 0.1s ease-in-out;

                &:hover{
                    border-bottom: 2px solid $color-tertiary;
                }
            }
        }
        .profile-pic{
            margin-right: 50px;
        }
       
        section{
            flex: 1;
            text-align: center;
            font-weight: bold;
            
            
        }
    }

    
    

}

.skills{
    display: flex;
    width: 100%;
    margin-top: 100px;
    justify-content: space-evenly;
    

}

@media (max-width: 600px){
    .about{
        h2{
            margin-top: 0;
            margin-bottom: 60px;
            
        }

        .img-div{
            display: none;
        }

        .about-p{
            font-size: 17px;
        }

        .about-info{
            p{
                text-align: center;
                line-height: 32px;
                
            }
        }
    }
    
    .skills{
        
        flex-wrap: wrap;
        width: 100%;
        margin-top: 80px;
        justify-content: space-evenly;
        p{
            font-size: 12px;
        }
    
    }

}
